.cardContainer {
  border-radius: 3px;
  padding: 24px 24px 16px;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.titleContainer {
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
  color: rgba(255, 255, 255, 1);
  text-align: right;
  margin-bottom: 16px;
}

.contentContainer {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;

  color: rgba(255, 255, 255, 1);
  text-align: right;
}

@media (max-width: 575.9px) {
  .cardContainer {
    height: fit-content;
    margin-bottom: 20px;
    padding-top: 26px;
    padding-bottom: 7px;
  }

  .titleContainer {
    font-weight: 500;
    font-size: 24px;
    line-height: 24px;
  }

  .collapsed {
    display: none;
  }
}
