.expandIcon {
  display: none;
  cursor: pointer;
  color: white;
  font-size: 40px;

  float: left;
  width: fit-content;

  position: absolute;
  left: 23px;
  top: 19px;
}

.expandIcon svg {
  width: 35px;
  height: 35px;
}

@media (max-width: 575.9px) {
  .expandIcon {
    display: inline-block;
  }
}
