.categoryCircle,
.categoryCircle > img {
  padding: 0;
  margin: 0;
  width: 136px;
  height: fit-content;
}

.categoryCircle > img {
  height: 136px;
}

.soon {
  pointer-events: none;
}

.label,
.soonlabel {
  color: white;
  text-align: center;
  font-size: 24px;
  line-height: 32px;
}

.soonlabel {
  font-size: 14px;
  font-weight: 400;
  margin-left: auto;
  margin-right: auto;
  margin-top: -19px;
}

@media (max-width: 768px) {

  .categoryCircle,
  .categoryCircle > img {
    width: 86px;
  }
  .categoryCircle > img {
    height: 86px;
  }

  .label {
    font-weight: 600;
    font-size: 14px;
    line-height: 32px;
  }
}
