.rollingAd {
  width: fit-content;
  width: -moz-max-content;
  min-width: 200px;
  height: 50px;
  background-color: #F5F5F5;
  display: inline-block;
  padding: 0px;
  cursor: pointer;
  color: black;
  margin-right: -2px;
}

.rollingAd:hover {
  color: black !important;
}

.rollingAd .rollingAdInner {
  padding-top: 10px;
  margin-top: 5px;
  font-weight: 700;
  font-size: 16px;
  display: inline-block;
}

.rollingAd .rollingAdInner svg {
  color: #05BEC0;
}

.rollingAdDivider {
  width: 1px;
  height: 70%;
  margin-top: 9px;
  background-color: #00000029;
  display: inline-block;
  margin-left: 15px;
  margin-right: 15px;
}


.rollingAdDividerRight {

  float: right;
}

.rollingAdDividerLeft {
  float: left;
}
