:global(main.mainContainer[data-page='/']),
:global(main.mainContainer[data-page^='/types']) {
  margin-top: -20px;
  min-height: fit-content;
  background-color: #F2F2F2;
}

.homeImage,
.homeImageGradient {
  width: 100%;
  display: inline-block;
}

.homeImage {
  background-image: url(/assets/img/background7.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.homeImageGradient {
  background: linear-gradient(180deg, rgba(0, 122, 157, 0.76) -8.57%, rgba(0, 122, 157, 0) 62.9%);
}

.homeImageContainer {
  background-color: white;
}

.homeImage .topRow {
  font-weight: 700 !important;
  font-size: 40px !important;
  line-height: 48px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  color: #F2F2F2;
  margin-top: 195px;
}

.homeImage .subTopRow {
  font-weight: 400 !important;
  font-size: 20px !important;
  text-align: center;
  color: #F2F2F2;
  max-width: 721px;
  margin-left: auto;
  margin-right: auto;
}

.goals {
  font-size: 20px;
}

.howItWorksTitle {
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
  color: #323232;
  padding-top: 32px;
  padding-bottom: 48px;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
}

.chosenDealsTitle {
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
  color: #323232;
  margin-top: 72px;
  margin-bottom: 48px;
}

.homeImage .circleLinks {
  max-width: 602px;
  margin: 40px auto 87px;
}


.horizontalExchangePath {
  display: block;
  width: 90%;
  height: 100%;
}

.verticalExchangePath {
  display: none;
  z-index: 1;
}

@media (max-width: 768px) {
  .goals {
    text-align: center;
  }
  .homeImage .topRow {
    font-size: 35px !important;
    margin-top: 90px;
  }
  .homeImage .circleLinks {
    margin-top: 34px;
    margin-bottom: 32px;
  }
}


@media (max-width: 576px) {


  .horizontalExchangePath {
    display: none;
  }

  .verticalExchangePath {
    display: block;
  }

  .exchangePathText {
    position: absolute;
    text-align: center;
    width: 191px;
    font-weight: 700;
    z-index: 2;
  }

  .exchangePathText.exchange1 {
    top: 25px;
  }

  .exchangePathText.exchange2 {
    top: 196px;
  }

  .exchangePathText.exchange3 {
    top: 355px;
  }

  .exchangePathText.exchange4 {
    top: 517px;
  }


  .exchangePathText.exchange5 {
    top: -464px;
    left: 12px;
  }

  .exchangePathText.exchange6 {
    top: -297px;
    left: 12px;
  }

  .exchangePathText.exchange7 {
    top: -154px;
    left: 12px;
  }

}

@media (max-width: 575.9px) {
  .homeImage .topRow {
    font-weight: bold !important;
    font-size: 24px !important;
    line-height: 34px;
  }

  .howItWorksTitle {
    font-size: 24px;
    line-height: 40px;
    padding-top: 24px;
    padding-bottom: 12px;
  }


  .chosenDealsTitle {
    font-size: 24px;
    line-height: 40px;
  }


  .exchangePathText {
    width: 37vw;
  }

}


